import React, { useState } from 'react'
import { Form } from 'antd'
export const InputRules = () => {
  const [form] = Form.useForm()
  const [username, setUserName] = useState('')
  const [usernameHelp, setUserNameHelp] = useState('')
  const [passwordHelp, SetPasswordHelp] = useState('')
  const [password, SetPassword] = useState('')
  const [email, SetEmail] = useState('')
  const [emailHelp, SetEmailHelp] = useState('')
  const [confirPsw, SetConfirPsw] = useState('')
  const [confirPswHelp, SetConfirPswHelp] = useState('')
  const [pswMessage, SetPasswordMeSSage] = useState('')

  const [isReload, SetisReload] = useState(false)

  const reloade = (PswValue: boolean) => {
    SetisReload(PswValue)
    if (isReload) {
      setUserName('')
      setUserNameHelp('')
      SetPassword('')
      SetPasswordHelp('')
      SetConfirPswHelp('')
      SetPasswordMeSSage('')
      SetEmail('')
      SetEmailHelp('')
      SetConfirPsw('')
      SetConfirPswHelp('')
    }
  }

  const eventB = (e: any) => {
    const { name, value } = e.target
    switch (name) {
      case 'username':
        if (!value) {
          setUserName('error')
          setUserNameHelp('')
        } else if (value.length > 10) {
          setUserName('error')
          setUserNameHelp('10')
        } else {
          setUserName('success')
          setUserNameHelp('...')
        }
        break
      case 'password':
        if (!value) {
          SetPassword('error')
          SetPasswordHelp('')
        } else {
          SetPassword('success')
          SetPasswordMeSSage(value)
          SetPasswordHelp('...')
        }
        break
      case 'email':
        if (!value) {
          SetEmail('error')
          SetEmailHelp('')
        } else {
          SetEmail('success')
          SetEmailHelp('...')
        }
        break
      case 'confirPsw':
        if (value === pswMessage && pswMessage !== '') {
          SetConfirPsw('success')
          SetConfirPswHelp('')
        } else if (pswMessage === '') {
          SetConfirPsw('error')
          SetConfirPswHelp('')
        } else {
          SetConfirPsw('error')
          SetConfirPswHelp('')
        }
        break
    }
  }

  return {
    reloade,
    eventB,
    username,
    password,
    email,
    confirPsw,
    usernameHelp,
    passwordHelp,
    emailHelp,
    confirPswHelp,
  }
}
